body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

* {
  margin: 0;
  padding: 0;
}
html {
  box-sizing: border-box;
}
.main-menu {
  background: #ffffff;
  border-bottom: solid 1px #dadada;
}
.main-menu ul {
  color: #313131;
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
  max-width: 1100px;
  margin: auto;
  height: 60px;
}

.main-menu ul li {
  font-size: 1.4rem;
  margin-right: 100px;
  padding: 1rem 0;
  text-align: center;
}

.navlink {
  color: #292929;
  text-decoration: none;
  border-bottom: 2px solid transparent;
  transition: all 0.5s;
}

.navlink:hover {
  border-bottom: 2px solid #292929;
}

.activeRoute {
  border-bottom: 2px solid #292929;
}

.google-logo {
  width: 2rem;
  height: 2rem;
  margin: auto;
}
.facebook-logo {
  width: 2rem;
  height: 2rem;
  color: #c92222;
}
.login-page {
  max-width: 840px;
  margin: auto;
  margin-top: 100px;
  margin-bottom: 100px;
}
.google-login {
  margin-bottom: 20px;
}
.oauth-login {
  font-size: 1.2rem;
  height: 4rem;
  cursor: pointer;
  text-decoration: none;
  margin: auto;
  color: #3b3b3b;
}

.user-info {
  display: grid;
  grid-template-columns: 360px auto;
  grid-column-gap: 30px;
  padding-bottom: 50px;
  border-bottom: solid 1px #dadada;
}
.username {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #000000dc;
  font-weight: 300;
  line-height: 40px;
  -webkit-align-self: center;
          align-self: center;
}

.profile-photo {
  width: 150px;
  border: 1px solid #ebeaea00;
  border-radius: 50%;
  justify-self: center;
}

.comment-list {
  list-style-type: none;
}
.single-comment {
  display: inline-block;
  padding: 20px 10px 30px 10px !important;
}

* {
  box-sizing: border-box;
}
/* body {
  padding: 0;
  margin: 0;
} */

.single-photo {
  max-width: 640px;
  margin: auto;
  margin-top: 100px;
  border: 1px solid #dadada;
  border-radius: 3px;
  color: #000000dc;
}

.single-photo__postedby {
  display: grid;
  grid-template-columns: 60px auto;
  height: 50px;
  -webkit-align-items: center;
          align-items: center;
}

.single-post__avatar-image {
  width: 40px;
  height: 40px;
  box-shadow: 0px 0px 0px 1px #c2c2c2;
  border-radius: 50%;
  justify-self: center;
}
.single-post__image {
  width: 100%;
}

.single-container {
  padding: 1rem;
}
.single-post__caption {
  margin: 10px auto 12px 16px;
  display: grid;
  grid-template-columns: auto 90px;
}
.single-post__author {
  font-weight: 600;
  margin-right: 5px;
}
.delete-btn {
  background: #c40000 !important;
  border: #c40000 !important;
  color: white;
  font-weight: 700;
  margin-right: 10px;
}

* {
  margin: 0;
  padding: 0;
}
html {
  box-sizing: border-box;
}

.postlist {
  display: grid;
  grid-template-columns: repeat(3, 360px);
  grid-row-gap: 20px;
  grid-column-gap: 30px;
  -webkit-align-items: center;
          align-items: center;
}
.postlist__card {
  height: 280px;
  width: 360px;
  background: black;
  border: solid #0f0f0f 1px;
  border-radius: 3px;
  display: grid;
}
.postlist__card__image {
  width: 100%;
  max-height: 100%;
  overflow: hidden;
  -webkit-align-self: center;
          align-self: center;
}

.addform__container {
  max-width: 720px;
  margin: auto;
  margin-top: 100px;
  border: 1px solid #d3d3d3;
  border-radius: 4px;
  background: white;
  padding: 20px 10px;
}
.form__input-group {
  display: grid;
  grid-template-rows: 1fr auto;
  grid-gap: 10px;
  margin-top: 20px;
}
.btn {
  padding: 5px 10px;
  font-size: 12px;
  background: white;
  border: 1px solid #909090;
  border-radius: 12px;
  cursor: pointer;
  text-transform: uppercase;
}
.add-btn {
  margin-top: 20px;
}

input[type='file'] {
  height: 20px;
}
textarea {
  background: #fafafa;
  border: 1px solid #afafaf;
  border-radius: 3px;
}

.container {
  max-width: 1140px;
  margin: auto;
  margin-top: 100px;
  margin-bottom: 100px;
}
body {
  background-color: #f6f6f6;
}

